import {useDispatch} from 'react-redux'
import {
  StyledAnchor,
  StyledBDLPage,
  StyledHead,
  StyledParagraph,
  StyledSection,
  StyledSectionHeader,
  StyledTbody,
  StyledTd,
  StyledTh,
  StyledTr,
} from './styled'
import {useLocation} from 'react-router-dom'
import {
  setProvinceNameBegin,
  setProvinceNameUnitBegin,
  setTeritorialUnitBegin,
} from '../teritorialUnit/teritorialUnitSlice'
import {
  setManyVariablesBegin,
  setProvinceNameVariablesBegin,
} from '../manyVariables/manyVariablesSlice'
import {useEffect} from 'react'
import {setSelectedMapBegin} from '../maps/mapsSlice'
function BDL() {
  const dispatch = useDispatch()
  const {pathname} = useLocation()
  useEffect(() => {
    pathname === '/bdl'
      ? dispatch(setTeritorialUnitBegin()) &&
        dispatch(setManyVariablesBegin()) &&
        dispatch(setSelectedMapBegin())
      : ''
  }, [])

  return (
    <StyledBDLPage>
      <StyledHead>Bank danych lokalnych</StyledHead>
      <StyledSection>
        <StyledSectionHeader>
          BANK DANYCH LOKALNYCH - najwiksza w Polsce baza danych o gospodarce,
          spoeczestwie i rodowisku.
        </StyledSectionHeader>
        <StyledParagraph>
          BDL oferuje ponad 40 tys. cech statystycznych pogrupowanych
          tematycznie. Pierwsze dane pochodz z 1995 roku.
        </StyledParagraph>
        <ul>
          <li>Dane i wskaniki opisuj powiaty, wojewdztwa i Polsk. </li>
          <li>
            Zasoby informacyjne (statystyki roczne i krtkookresowe) s na
            bieco uzupeniane oraz aktualizowane.
          </li>
        </ul>
        <StyledParagraph>
          Aplikacja API pozwala przeglda i pobiera do dalszego przetwarzania
          peny zakres danych znajdujcych si w Banku. API do BDL zrealizowano
          w ramach partnerskiego projektu Otwarte dane - dostp, standard,
          edukacja, ktrego Liderem jest Ministerstwo Cyfryzacji. Projekt jest
          wspfinansowany ze rodkw Unii Europejskiej z Programu Operacyjnego
          Polska Cyfrowa.
        </StyledParagraph>
      </StyledSection>
      <StyledSection>
        <StyledSectionHeader>Limity zapyta</StyledSectionHeader>
        <StyledParagraph>
          Ze wzgldw bezpieczestwa API na ilo da naoone s limity
          ilociowe.
        </StyledParagraph>
        <StyledParagraph>
          <StyledTbody>
            <StyledTr>
              <StyledTh>Okres</StyledTh>
              <StyledTh>Ilo zapyta</StyledTh>
            </StyledTr>
            <StyledTr>
              <StyledTd>1s</StyledTd>
              <StyledTd>5</StyledTd>
            </StyledTr>
            <StyledTr>
              <StyledTd>15m</StyledTd>
              <StyledTd>100</StyledTd>
            </StyledTr>
            <StyledTr>
              <StyledTd>12h</StyledTd>
              <StyledTd>1&nbsp;000</StyledTd>
            </StyledTr>
            <StyledTr>
              <StyledTd>7d</StyledTd>
              <StyledTd>10&nbsp;000</StyledTd>
            </StyledTr>
          </StyledTbody>
        </StyledParagraph>
      </StyledSection>
      <StyledSection>
        <StyledSectionHeader>Licencja</StyledSectionHeader>
        <StyledParagraph>
          Dane s moliwe do wykorzystywania w oparciu o licencj midzynarodow
          Creative Commons by 4.0 - Uznanie autorstwa
        </StyledParagraph>
        <StyledAnchor href="https://creativecommons.org/licenses/by/4.0/legalcode.pl">
          https://creativecommons.org/licenses/by/4.0/legalcode.pl
        </StyledAnchor>
        <StyledParagraph>
          Wacicielem aplikacji API udostpniajcej dane jest Gwny Urzd
          Statystyczny
        </StyledParagraph>
        <StyledAnchor href="https://api.stat.gov.pl/Home/BdlApi">
          https://api.stat.gov.pl/Home/BdlApi
        </StyledAnchor>
      </StyledSection>
    </StyledBDLPage>
  )
}
export default BDL
